@use "../../assets/sass/variables" as *;
.card {
    border: 1px solid $border;
    background-color: $main-card;
    box-shadow: 5px 10px 8px $border;
    width: 200px;
    height: 450px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &__name {
        color: $main-heading;
        text-align: center;
        font-family: $font-headings;
        font-size: 1em;
    }&__tagline {
        font-size: 1em;
        text-align: center;
        font-family: $font-paragraphs;
        color: $main-paragraph;
    }
    &__image {
        width: 4vw;
        height: auto
    }&__stats {
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        text-align: left;
        &--heading {
            font-family: $font-headings;
            color: $main-heading;
        }
        &--list {
            font-family: $font-paragraphs;
            color: $main-paragraph;
        }
    }
}